.skillsBlock {
    min-height: 100vh;
    background-color: mediumaquamarine;
}

.skillsBlock h2 {
    background-color: #fff;
    margin-bottom: 30px;
}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}